  max-height: 400px;
}

.toyImg {
  width: 70%; 
  padding-top: 20px;
  margin: 0 15%;
}

.descContainer {
  display: flex;
  background-color: white;
  color: black;
  font-family: 'Montserrat', 'Times New Roman', serif;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 120px;
}

.toyDesc {
  text-align: center;
  font-size: 16px;
  letter-spacing: 2px;
  margin: auto;
}

.left, .right {  
  cursor: pointer;  
  position: absolute;  
  top: 50%;  
  width: auto;  
  padding: 16px;  
  margin-top: -22px;  
  color: #e6e6e6;  
  font-size: 24px;  
  transition: 0.6s ease;  
  border-radius: 0 3px 3px 0;  
}  
.right {  
  right: 0;  
  border-radius: 3px 0 0 3px;  
}  
  .left:hover, .right:hover {  
      background-color: rgba(115, 115, 115, 0.5);  
  }  

  /* Fading animation */  
  .fade {  
    -webkit-animation-name: fade;  
    -webkit-animation-duration: 1.5s;  
    animation-name: fade;  
    animation-duration: 1.5s;  
  }  
  @-webkit-keyframes fade {  
    from {  
        opacity: .4  
    }  
    to {  
        opacity: 1  
    }  
  }  
  @keyframes fade {  
    from {  
        opacity: .1  
    }  
    to {  
        opacity: 1  
    }  
  }  
  
  .slide {
    display: none;
  }

  .active-slide-0 #slide-0 {
    display: block;
  }
  .active-slide-1 #slide-1 {
    display: block;
  }
  .active-slide-2 #slide-2 {
    display: block;
  }
  .active-slide-3 #slide-3 {
    display: block;
  } 
  .active-slide-4 #slide-4 {
    display: block;
  }
  .active-slide-5 #slide-5 {
    display: block;
  }
  .active-slide-6 #slide-6 {
    display: block;
  }
  .active-slide-7 #slide-7 {
    display: block;
  }
  .active-slide-8 #slide-8 {
    display: block;
  } 
  .active-slide-9 #slide-9 {
    display: block;
  }



